
.loader{position:fixed;top:0;left:0;width:0;height:100%;background-color:rgba(255,255,255,0);overflow:hidden;z-index:9999;transition:all .2s ease-out
}
.loader .spinner{width:40px;height:40px;position:relative;margin:auto;top:calc( 50% - ( 20px / 2 ))
}
.loader .double-bounce1,.loader .double-bounce2{width:100%;height:100%;border-radius:50%;background-color:#ee8c32;opacity:.8;position:absolute;top:0;left:0;animation:sk-bounce 2.0s infinite ease-in-out
}
.loader .double-bounce2{animation-delay:-1.0s
}
@keyframes sk-bounce{
0%,100%{transform:scale(0);-webkit-transform:scale(0)
}
50%{transform:scale(1);-webkit-transform:scale(1)
}
}
.loader.open{width:100%;background-color:#fff;transition:all .1s ease-in
}



